@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*Reset*/
* {
  padding: 0;
  margin: 0;
  line-height: 1;
  box-sizing: border-box;
}

a, img, button, input {
  display: block;
}

a, img, button {
  cursor: pointer;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

a {
  text-decoration: none;
}

button:focus, input:focus {
  outline: none;
}

html {
  font-size: 10px;
  font-family: Roboto;
}

.app {
  width: 100%;
  height: 100vh;
  background-color: #EEF2F5;
}

.app__wrap {
  width: 100%;
  height: 100%;
  position: relative;
}

.main__bg__big, .main__bg__mid {
  height: 100%;
  width: auto;
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
}

.bg__big, .bg__mid, .bg__mid_2 {
  width: 100%;
  position: absolute;
  bottom: 0;
}

.main__bg__mid, .bg__mid, .bg__img, .bg__mid_2, .bg__img_min {
  display: none;
}

.form__wrap {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 2.4rem;
  z-index: 2;
}

.form__container {
  width: 39.97%;
  padding: 8.8rem 0;
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  background-color: #FEFEFE;
}

@media screen and (min-width: 1600px) {
  html {
    font-size: 12px;
  }
}

@media screen and (max-width: 1201px) {
  .form__wrap {
    padding-right: 1.6rem;
  }
  .form__container {
    width: 48.41%;
    min-width: 42rem;
  }
  .main__bg__big, .bg__big {
    display: none;
  }
  .main__bg__mid, .bg__mid {
    display: block;
  }
  .main__bg__mid {
    height: 100%;
  }
}

@media screen and (max-width: 769px) {
  .form__wrap {
    padding-right: 0;
    justify-content: center;
  }
  .form__container {
    width: 71.35%;
    min-width: 42rem;
  }
  .main__bg__mid, .bg__mid {
    display: none;
  }
  .bg__mid_2, .bg__img {
    display: block;
  }
  .bg__img {
    position: absolute;
    z-index: 1;
  }
  .bg__img#bg__img_1 {
    width: 28.25%;
    left: 4.56%;
    top: -1.27%;
  }
  .bg__img#bg__img_2 {
    width: 12.63%;
    left: 1.43%;
    top: 54.1%;
  }
  .bg__img#bg__img_3 {
    bottom: 2.58%;
    left: 1.43%;
    width: 23.83%;
  }
  .bg__img#bg__img_4 {
    bottom: 0;
    left: 36.32%;
    width: 30.86%;
  }
  .bg__img#bg__img_5 {
    width: 7.8125%;
    right: 7.044%;
    top: 7.8125%;
  }
  .bg__img#bg__img_6 {
    width: 15.23%;
    right: 0%;
    top: 54.1%;
  }
  .bg__img#bg__img_7 {
    bottom: -1.9%;
    width: 28.78%;
    right: 3.5%;
  }
}

@media screen and (max-width: 575px) {
  .form__container {
    min-width: 34.3rem;
    padding: 4rem 0 5.6rem;
  }
  .bg__img {
    display: none;
  }
  .bg__img_min {
    display: block;
    position: absolute;
  }
  .bg__img_min#bg__img_8 {
    width: 29.87%;
    top: 0;
    left: 7.528%;
  }
  .bg__img_min#bg__img_9 {
    width: 20.8%;
    bottom: 0.712%;
    left: 4.58%;
  }
  .bg__img_min#bg__img_10 {
    width: 27.73%;
    bottom: 0.712%;
    right: 0%;
  }
}

@media screen and (max-width: 374px) {
  .form__container {
    min-width: 0;
    width: 90%;
  }
}
