.login__form {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.form__title {
  margin-bottom: 6.4rem;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 3.0rem;
  line-height: 1.9rem;
  text-transform: uppercase;
  color: #10204C;
}

.field__block {
  width: 26rem;
  position: relative;
  margin-bottom: 3.2rem;
}

.field__block:last-child {
  margin-bottom: 11rem;
}

.field__block input {
  width: 100%;
  padding: 1.4rem 1.4rem;
  border: 1px solid #B2B7BB;
  border-radius: 8px;
  font-size: 1.6rem;
  line-height: 1.8rem;
}

.field__block input::-webkit-input-placeholder {
  color: #B2B7BB;
}

.field__block input:-ms-input-placeholder {
  color: #B2B7BB;
}

.field__block input::placeholder {
  color: #B2B7BB;
}

.field__block .show_pass__btn {
  position: absolute;
  width: 1.8rem;
  top: 50%;
  right: 1.5rem;
  transform: translateY(-50%);
}

.field__block .show_pass__btn img {
  width: 100%;
}

.field__block .error__message {
  position: absolute;
  bottom: 0;
  transform: translateY(100%);
  padding: 0.4rem 0 0 0;
  left: 1.4rem;
  font-size: 1.4rem;
  font-family: Roboto;
  line-height: 1.9rem;
  color: #F4604D;
}

.field__block.error input {
  border: 1px solid #F4604D;
}

.field__block.error input::-webkit-input-placeholder {
  color: #F4604D;
}

.field__block.error input:-ms-input-placeholder {
  color: #F4604D;
}

.field__block.error input::placeholder {
  color: #F4604D;
}

.submit__btn {
  width: 21.2rem;
  padding: 1.5rem 0;
  font-size: 1.8rem;
  font-family: Roboto;
  font-weight: 500;
  text-transform: uppercase;
  color: #FEFEFE;
  background: linear-gradient(90deg, #FF9146 0%, #FF351B 100%);
  border-radius: 5px;
}

.submit__btn[disabled] {
  background: #B2B7BB;
}

.form__text {
  margin-top: 1.6rem;
  width: 100%;
  display: flex;
  justify-content: center;
}

.form__text a, .form__text p {
  font-family: Roboto;
  font-size: 1.6rem;
  line-height: 150%;
  letter-spacing: 0.005em;
  color: #B2B7BB;
}

.form__text .forgot__pass {
  text-decoration: underline;
}

.form__text .register {
  color: #4285F4;
  margin-left: 0.9rem;
}

@media screen and (max-width: 575px) {
  .form__title {
    margin-bottom: 5rem;
    font-size: 2.4rem;
    line-height: 1.9rem;
  }
  .field__block {
    width: 26rem;
    margin-bottom: 3.6rem;
  }
  .field__block:last-child {
    margin-bottom: 8.6rem;
  }
  .field__block input {
    padding: 1rem 1.4rem;
  }
  .submit__btn {
    width: 16.3rem;
    padding: 1.1rem 0;
    font-size: 1.8rem;
  }
}
/*# sourceMappingURL=Form.css.map */
